function LrcPlayer(e, container) {
  this._e = e;
  this._lrc = null
  this.line = " ";
  this.lyrics = [];
  this.tim = [];
  this.container = container;
  this.cPLrc = null;
  this.currentLrc = "sadas";
  this.index = 0;

  this.loadLrc = function (lrc) {
    this._lrc = lrc
    this._parser()
  }

  this._parser = function () {
    let lrcLines = this._lrc.split(/\r\n|\n/);
    for (let i = 0; i < lrcLines.length; i++) {
      if (lrcLines[i].search(/^(\[)(\d*)(:)(.*)(\])(.*)/i) >= 0) {
        this.line = lrcLines[i].match(/^(\[)(\d*)(:)(.*)(\])(.*)/i);
        this.tim[i] = (parseInt(this.line[2]) * 60) + parseInt(this.line[4]);
        this.lyrics[i] = this.line[6];
        console.log(this.line)
        this.container.innerHTML += `<p>${this.lyrics[i]}</p>`
      }
    }


    let temp = [];

    for (let i = 0; i < this.tim.length; i++) {
      if(null != this.tim[i])
        temp.push(this.tim[i])
    }

    this.tim = temp;

    temp = [];

    for (let i = 0; i < this.lyrics.length; i++) {
      if (null != this.lyrics[i])
        temp.push(this.lyrics[i])
    }

    this.lyrics = temp;

    console.log(this.tim)
    console.log(this.lyrics)
  }

  this._player = function (currentTime) {

    for (let i = 0; i < this.tim.length; i++) {
      if (currentTime > this.tim[i] && currentTime < this.tim[i + 1]) {
        if (this.currentLrc.indexOf(this.lyrics[i]) == -1) {
          let pLrsList = this.container.querySelectorAll("p");
          if (this.cPLrc)
            this.cPLrc.classList.remove("lrc-active")
          pLrsList[i].classList.add("lrc-active")
          // this.container.innerHTML = `<p>${this.lyrics[i]}</p>`
          document.title = this.lyrics[i];
          this.cPLrc = pLrsList[i];

          this.index = 38 * i;
          this.currentLrc = this.lyrics[i];
          window.scrollTo(0, this.index)
        } else {
          console.log("休息中")
        }

      }
    }
  }
  this._e.addEventListener("timeupdate", () => {
    this._player(this._e.currentTime)
  })


}

module.exports = {
  LrcPlayer: LrcPlayer
}
